@import url(https://fonts.googleapis.com/css2?family=Playfair+Display:wght@400;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Sans:wght@400;700&display=swap);

/* contentComponentBody */
.contentBody {padding-top:80px;}
.contentBody h2{margin:0; padding: 0;}


.timeline{font-style: italic;}

.m1{margin:4px;}
.m2{margin:8px;}

#navigation-bar{
  display:flex;
  flex-wrap: nowrap;
  flex-basis:auto;
  list-style: none;
  margin-right:36px;
  float:right;
}

#navigation-bar li{
  margin:10px;

}
#navigation-bar a{
  font-family: 'Playfair Display', serif;
  font-weight: bold;
  text-decoration: none;
  font-size: 1.4em;
}
#navigation-bar a:hover{
  text-decoration: underline;
}

.caseStudyGrid{
    display: flex;
    flex-wrap: nowrap;
    flex-basis: auto;
    flex-direction: row;
    list-style: none;
    padding:0;
    margin-top:2%;
}


a.cardwrapper{
  width:33%;
  margin:0 2% 0 0;
  background-color: #fff;
  text-decoration: none;
}

a.cardwrapper:hover{
  box-shadow: 1px 1px 10px 4px #ccc;
  transform: scale(1.01);
}

.cardBody img{
  width:100%;
}

.cardfooter{
  padding:3.5% 4.5% 5.5%;
}
.cardfooter span{
  font-weight: normal;
  font-size:.8em;
  display:block;
  padding:5px 0 10px;
}

a .cardfooter {
  font-weight: bold;
  color:#333;
}

a.a.cardwrapper{
  width:33%;
  margin:0 2% 0 0;
  background-color: #fff;
  text-decoration: none;
}
a.cardwrapper{
  width:33%;
  margin:0 2% 0 0;
  background-color: #fff;
  text-decoration: none;
}



article, aside, details, figcaption, figure, footer, header, hgroup, main, menu, nav, section, summary {
    display: block;
}
 /*type and headlines*/

h1, h2, h3{
  font-family: 'Playfair Display', serif;
  font-weight: bold;
  letter-spacing: -1px;
  }

h1{font-size: 1.4em;}
h2{font-size: 2.5em;}
h3{font-size: 1.5em;}
h4{font-size: 1.3em; margin:0 0 10px;}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

a{color:#E63946;}

.deliniator{font-weight: bold; font-size:4em; padding:0; margin:0; position: relative; top:-30px; color:#999; display: block; height:40px;}

#main-content{
  clear: both;
  margin:100px auto 0;
  background-color: #ececec;
  padding:0 8% 8%;
}

body {
  margin: 0;
  font-family: 'Noto Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


.homelink{
  margin:24px 36px;
  display: block;
  float:left;
  text-decoration: none;
}

.homelink h1{
  display: inline;
}

h5.subhead{background-color:#d2d2d2; padding:3px 10px; text-transform: uppercase; display: inline;}

